import React from 'react';
import './styles/global.scss'
import Home from './Home'
import Welcome from './Welcome'
import { Route, Switch } from 'react-router-dom';

function App() {
  return (
    <div className="App">
      <Switch>
      <Route component={Home} path="/home" />
      <Route component={Welcome} path="/" />
      </Switch>
    </div>
  );
}

export default App;
